.brk-forum-posts {
  display: flex;
  flex-direction: column;
  position: relative;
  padding-left: 32px;
  padding-top: 7px; }
  [dir="rtl"] .brk-forum-posts {
    padding-left: 0;
    padding-right: 32px; }
  .brk-forum-posts::before {
    content: '';
    position: absolute;
    left: 30px;
    top: 0;
    height: 100%;
    width: 1px;
    background-color: #ebebeb; }
    [dir="rtl"] .brk-forum-posts::before {
      left: auto;
      right: 30px; }
  .brk-forum-posts .brk-forum-post {
    padding-left: 51px; }
    [dir="rtl"] .brk-forum-posts .brk-forum-post {
      padding-left: 0;
      padding-right: 51px; }
  .brk-forum-posts .brk-forum-post__header {
    margin-left: -85px; }
    [dir="rtl"] .brk-forum-posts .brk-forum-post__header {
      margin-left: 0;
      margin-right: -85px; }
  .brk-forum-posts .brk-reply {
    padding-left: 51px; }
  .brk-forum-posts .brk-reply__header {
    margin-left: -51px;
    padding-left: 51px; }
  @media screen and (max-width: 576px) {
    .brk-forum-posts {
      padding-left: 0; }
      .brk-forum-posts::before {
        display: none; }
      .brk-forum-posts .brk-forum-post {
        padding-left: 0; }
        .brk-forum-posts .brk-forum-post__header {
          margin-left: 0; }
      .brk-forum-posts .brk-reply {
        padding-left: 0; }
        .brk-forum-posts .brk-reply__header {
          padding-left: 0;
          margin-left: 0;
          text-align: center; } }

.brk-forum-post {
  padding-bottom: 30px;
  padding-top: 25px;
  border-image: url("../../img/dashed-border.png") 0 0 2 388 repeat;
  border-bottom: 1px solid; }
  .brk-forum-post:last-of-type {
    border-bottom: 0; }
  .brk-forum-post.no-border {
    border-bottom: 0; }
  .brk-forum-post__header {
    display: flex;
    align-items: flex-start; }
    .brk-forum-post__header-info {
      margin-top: 15px;
      margin-bottom: 10px;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      flex-grow: 1; }
      .brk-forum-post__header-info > *:not(:last-child) {
